body {
  font-family: sans-serif;
  font-size: 16px;
  min-width: 320px;
  position: relative;
  line-height: 1.6;
  font-family: "HeliosLight";
  overflow-x: hidden;
  margin: 0;
  padding: 0; }

ul {
  list-style-type: none;
  margin: 0;
  padding: 0; }

a:active,
a:visited,
a:focus {
  text-decoration: none; }

.slick-next {
  font-size: 0;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  left: 0px;
  background-image: url(../img/slider-arrow-left.png);
  width: 27px;
  height: 51px;
  background-color: transparent;
  border: none;
  outline-style: none;
  z-index: 10; }

.slick-prev {
  font-size: 0;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  right: 0px;
  background-image: url(../img/slider-arrow-right.png);
  width: 27px;
  height: 51px;
  background-color: transparent;
  border: none;
  outline-style: none;
  z-index: 10; }

.main-form input[placeholder] {
  color: #d7d7d7; }

.main-form input::-webkit-input-placeholder {
  color: #d7d7d7; }

.main-form input:-moz-placeholder {
  color: #d7d7d7; }

.main-form input:focus::-webkit-input-placeholder {
  color: transparent; }

.main-form input:focus::-moz-placeholder {
  color: transparent; }

.main-form input:focus:-moz-placeholder {
  color: transparent; }

.main-form input:focus:-ms-input-placeholder {
  color: transparent; }

.white-popup {
  position: relative; }

.toggle-mnu {
  display: none;
  width: 28px;
  height: 28px;
  margin-top: 5px;
  position: absolute;
  top: 0;
  left: 30px;
  z-index: 100; }
  @media only screen and (max-width: 768px) {
    .toggle-mnu {
      display: block; } }
  .toggle-mnu span {
    position: relative;
    display: block;
    width: 100%;
    height: 2px;
    background-color: #000;
    transition: all 0.3s;
    backface-visibility: hidden;
    border-radius: 2px; }
    .toggle-mnu span:after, .toggle-mnu span:before {
      content: "";
      position: absolute;
      left: 0;
      top: 9px; }
    .toggle-mnu span:after {
      top: 18px;
      width: 100%;
      height: 2px;
      background-color: #000;
      transition: all 0.3s;
      backface-visibility: hidden;
      border-radius: 2px; }
    .toggle-mnu span:before {
      width: 100%;
      height: 2px;
      background-color: #000;
      transition: all 0.3s;
      backface-visibility: hidden;
      border-radius: 2px; }
  .toggle-mnu.on span {
    background-color: transparent; }
    .toggle-mnu.on span:before {
      transform: rotate(45deg) translate(-1px, 0px); }
    .toggle-mnu.on span:after {
      transform: rotate(-45deg) translate(6px, -7px); }

#map {
  max-width: 800px;
  height: 400px; }

.scrolltop {
  display: none;
  bottom: 30px;
  height: 42px;
  background-color: #000;
  border-radius: 50%;
  position: fixed;
  right: 30px;
  width: 42px;
  z-index: 10;
  cursor: pointer;
  transition: all 300ms ease-in; }
  .scrolltop::after {
    position: absolute;
    content: "";
    width: 10px;
    height: 10px;
    box-shadow: inset -1px -1px 0 1px white;
    top: 52%;
    left: 51%;
    transform: translate(-50%, -50%) rotate(-135deg);
    border-radius: 50px 0 0 0;
    background-color: transparent; }
  .scrolltop:active {
    background-color: #000; }

.scrolltop:hover {
  background-color: #000; }

.scrolltop a {
  display: block;
  width: 100%;
  height: 100%; }
